import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQueryCafe, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-list-discount',
  templateUrl: './list-discount.component.html',
  styleUrls: ['./list-discount.component.scss']
})
export class ListDiscountComponent implements OnInit {
  list_discount: any[] = [];
  show_add_discount_modal = false;
  show_edit_discount_modal = false;
  form_add: FormGroup
  form_edit: FormGroup
  level1: any = { sales: 0, discount: 0 } // bin dng  x l mc 1
  level2: any = { sales: 0, discount: 0 }// bin dng  x l mc 2
  level3: any = { sales: 0, discount: 0 }// bin dng  x l mc 3
  level4: any = { sales: 0, discount: 0 }// bin dng  x l mc 4
  level5: any = { sales: 0, discount: 0 }// bin dng  x l mc 4
  discount_selected: any;

  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private languageService: LanguageService,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private nzModalService: NzModalService
  ) { }

  ngOnInit(): void {
    this.initFormAdd();
  }

  ngAfterViewInit(): void {
    this.getData();
  }

  /**
   * get danh sch chit khu doanh s
   */
  getData() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQueryCafe.getDiscount_saless().then((response: any[]) => {
        this.list_discount = this.vhAlgorithm.sortStringbyASC(response, 'name')
        console.log( this.list_discount);
        
        this.vhComponent.hideLoading(0)
      })
    })
  }

  /**
   * khi to form lu cc trng ca chit khu doanh s  thm
   */
  initFormAdd() {
    this.form_add = new FormGroup({
      name: new FormControl('', Validators.required),
      type: new FormControl(1, Validators.required), // 1: %, 2: tin
      method: new FormControl(2, Validators.required), // 1: theo mc,2 : mc cui
      level1: new FormControl({ sales: 0, discount: 0 }, Validators.required),
      level2: new FormControl({ sales: 0, discount: 0 }, Validators.required),
      level3: new FormControl({ sales: 0, discount: 0 }, Validators.required),
      level4: new FormControl({ sales: 0, discount: 0 }, Validators.required),
      level5: new FormControl({ sales: 0, discount: 0 }, Validators.required),
    })
    this.level1 = { sales: 0, discount: 0 }
    this.level2 = { sales: 0, discount: 0 }
    this.level3 = { sales: 0, discount: 0 }
    this.level4 = { sales: 0, discount: 0 }
    this.level5 = { sales: 0, discount: 0 }
  }

  /**
   * gn gi tr chit khu vo form  cp nht
   */
  initFormEdit(discount) {
    this.form_edit = new FormGroup({
      _id: new FormControl(discount._id, Validators.required),
      name: new FormControl(discount.name, Validators.required),
      type: new FormControl(discount.type, Validators.required),
      method: new FormControl(discount.method, Validators.required),
      level1: new FormControl({ sales: discount.level1.sales, discount: discount.level1.discount }, Validators.required),
      level2: new FormControl({ sales: discount.level2.sales, discount: discount.level2.discount }, Validators.required),
      level3: new FormControl({ sales: discount.level3.sales, discount: discount.level3.discount }, Validators.required),
      level4: new FormControl({ sales: discount.level4.sales, discount: discount.level4.discount }, Validators.required),
      level5: new FormControl({ sales: discount.level5.sales, discount: discount.level5.discount }, Validators.required),
    })
    this.level1 = discount.level1
    this.level2 = discount.level2
    this.level3 = discount.level3
    this.level4 = discount.level4
    this.level5 = discount.level5
    this.show_edit_discount_modal = true;
  }

  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - 50 + "px";
    }
    this.cdRef.detectChanges();
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/sales-program/discount-customer/sales-discount'])
  }

  addDiscount() {
    this.addDiscountDB();
  }


  /**
   * thm chit khu doanh s vo DB
   */
  addDiscountDB() {
    this.form_add.controls['level1'].setValue(this.level1);
    this.form_add.controls['level2'].setValue(this.level2);
    this.form_add.controls['level3'].setValue(this.level3);
    this.form_add.controls['level4'].setValue(this.level4);
    this.form_add.controls['level5'].setValue(this.level5);
    if (this.form_add.valid)
      this.vhComponent.showLoading('').then(() => {
        this.vhQueryCafe.addDiscount_sales(  this.form_add.value).then((response: any) => {
          this.show_add_discount_modal = false
          this.vhComponent.alertMessageDesktop('success', this.languageService.translate("Thm cng thc chit khu thnh cng"));
          this.list_discount = this.list_discount.concat([response]);
          
            this.vhComponent.hideLoading(0);
        }, error => {
          console.log('error', error);
          this.vhComponent.hideLoading(0)
          this.vhComponent.alertMessageDesktop('error', this.languageService.translate("Thm cng thc chit khu tht bi"));
        })
      })
    else this.vhComponent.alertMessageDesktop('error', this.languageService.translate("Vui lng in y  thng tin"));
  }

  saveDiscount() {
    this.updateDiscountDB()
    // if(!this.level2.sales && this.level3.sales ) this.addDiscountDB()
    // else if(this.level1.sales < this.level2.sales && this.level2.sales > 0){

    // }


  }

  /**
   * cp nht chit khu doanh s ln DB khi nhn nt lu ti modal cp nht cng thc chit khu
   */
  updateDiscountDB() {
    this.form_edit.controls['level1'].setValue(this.level1);
    this.form_edit.controls['level2'].setValue(this.level2);
    this.form_edit.controls['level3'].setValue(this.level3);
    this.form_edit.controls['level4'].setValue(this.level4);
    this.form_edit.controls['level5'].setValue(this.level5);
    this.vhComponent.showLoading('').then(() => {
      this.vhQueryCafe.updateDiscount_sales( this.form_edit.value._id, this.form_edit.value).then((bool) => {
        this.show_edit_discount_modal = false
        if (bool) {
          this.list_discount[this.list_discount.indexOf(this.discount_selected)] = this.form_edit.value;
          this.vhComponent.hideLoading(0);
        }
      }, error => {
        console.log('error', error);
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Cp nht tht bi. Vui lng th li sau!"))
      })
    })
  }

  /**
   * Hy ko lu chit khu
   */
  cancelEdit() {
    this.show_edit_discount_modal = false;
    this.list_discount[this.list_discount.indexOf(this.discount_selected)].level1 = this.form_edit.value.level1;
    this.list_discount[this.list_discount.indexOf(this.discount_selected)].level2 = this.form_edit.value.level2;
    this.list_discount[this.list_discount.indexOf(this.discount_selected)].level3 = this.form_edit.value.level3;
    this.list_discount[this.list_discount.indexOf(this.discount_selected)].level4 = this.form_edit.value.level4;
    this.list_discount[this.list_discount.indexOf(this.discount_selected)].level5 = this.form_edit.value.level5;
  }

  /**
   * xa cng thc chit khu khi DB
   */
  deleleDiscount(data) {
    
    this.vhComponent.showLoading('').then(() => {
      let customer_discount = this.vhQueryCafe.getlocalCustomers().filter(i => i.id_discount_sales == data._id)
      console.log(customer_discount);
      let promise = new Array();
      for (let i = 0; i < customer_discount.length; i++) {
        promise[i] = this.vhQueryCafe.updateCustomer(customer_discount[i]._id, { id_discount_sales: '' })
      }
      this.vhQuery.asyncPromiseAll(promise)
        .then((array) => {
          this.vhQueryCafe.deleteDiscount_sales(data._id).then(bool => {
            this.vhComponent.hideLoading(0)
            if (bool) {
              this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Xa chit khu thnh cng"));
              this.list_discount = this.list_discount.filter(item => item._id != data._id)
            }
            else this.vhComponent.alertMessageDesktop("error", this.languageService.translate("C li xy ra khi xa. Vui lng th li sau!"));
          })
        }, error => { this.vhComponent.hideLoading(0);console.error(error);
         })

    })
  }

  /**
   * check price hp l
   * @param item 
   * @param fieldName 
   * @param event 
   */
  checkEditPrice(item, fieldName, event) {
    if (event)
      if (fieldName == 'discount' && this.form_add.value.type == 1) item[fieldName] = parseFloat(event.toString().replaceAll(',', '')) < 0 || parseFloat(event.toString().replaceAll(',', '')) > 100 ? 0 : parseFloat(parseFloat(event.toString().replaceAll(',', '')).toFixed(2))
      else item[fieldName] = parseFloat(event.toString().replaceAll(',', '')) < 0 ? 0 : parseFloat(event.toString().replaceAll(',', ''))
    else item[fieldName] = 0
  }


  /**
   * check price hp l
   * @param item 
   * @param fieldName 
   * @param event 
   */
   checkEditPriceUpdate(item, fieldName, event) {
    if (event)
      if (fieldName == 'discount' && this.form_edit.value.type == 1) item[fieldName] = parseFloat(event.toString().replaceAll(',', '')) < 0 || parseFloat(event.toString().replaceAll(',', '')) > 100 ? 0 : parseFloat(parseFloat(event.toString().replaceAll(',', '')).toFixed(2))
      else item[fieldName] = parseFloat(event.toString().replaceAll(',', '')) < 0 ? 0 : parseFloat(event.toString().replaceAll(',', ''))
    else item[fieldName] = 0
  }

   /**
   * kim tra doanh s mc 2 phi ln hn mc 1
   */
    checkSalesLevel1() {
      if (this.level1.sales > this.level2.sales && this.level2.sales) {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
        this.level1.sales = this.level2.sales - 1
      }
      if (this.level2.sales < this.level1.sales && this.level2.discount) {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
        this.level1.sales = this.level2.sales - 1
      }
    }
  /**
   * kim tra doanh s mc 2 phi ln hn mc 1
   */
  checkSalesLevel2() {
    if (this.level2.sales < this.level1.sales && this.level2.sales) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level2.sales = this.level1.sales + 1
    }
    if (this.level2.sales < this.level1.sales && this.level2.discount) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level2.sales = this.level1.sales + 1
    }
  }

  /**
  * kim tra doanh s mc 3 phi ln hn mc 2
  */
  checkSalesLevel3() {
    this.checkSalesLevel2()
    if (this.level3.sales < this.level2.sales && this.level3.sales) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level3.sales = this.level2.sales + 1
    }
    if (this.level3.sales < this.level2.sales && this.level3.discount) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level3.sales = this.level2.sales + 1
    }
    if (!this.level2.sales && !this.level2.discount && (this.level3.sales || this.level3.discount)) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level3 = { sales: 0, discount: 0 }
    }
  }

  /**
   * kim tra doanh s mc 4 phi ln hn mc 3
   */
  checkSalesLevel4() {
    this.checkSalesLevel2()
    this.checkSalesLevel3()
    if (this.level4.sales < this.level3.sales && this.level4.sales) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level4.sales = this.level3.sales + 1
    }
    if (this.level4.sales < this.level3.sales && this.level4.discount) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level4.sales = this.level3.sales + 1
    }
    if (!this.level3.sales && !this.level3.discount && (this.level4.sales || this.level4.discount)) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level4 = { sales: 0, discount: 0 }
    }
  }

  checkSalesLevel5() {
    this.checkSalesLevel2()
    this.checkSalesLevel3()
    this.checkSalesLevel4()
    if (this.level5.sales < this.level4.sales && this.level5.sales) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level5.sales = this.level4.sales + 1;
    }
    if (this.level5.sales < this.level4.sales && this.level5.discount) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level5.sales = this.level4.sales + 1;
    }
    if (!this.level4.sales && !this.level4.discount && (this.level5.sales || this.level5.discount)) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng nhp doanh s tng dn theo tng mc"))
      this.level5 = { sales: 0, discount: 0 }
    }
  }
  /**
   * reset li 3 loi mc khi thay i kiu chit khu
   */
  changeType() {
    this.level1.discount = 0
    this.level2.discount = 0
    this.level3.discount = 0
    this.level4.discount = 0
    this.level5.discount = 0
  }

}
